import server from '../../server.js';
export default {
	data() {
		return {
			TabCur:0,
			msgList:[]
		}
	},
	onLoad:function(){
		uni.showLoading({
			title: '加载中'
		});
		uni.request({
			method:'GET',
			url: "https://api.angeli.top/user.php?type=getMypl", //仅为示例，并非真实接口地址。
			data: {
				token:server.token
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				
			},
			success: (res) => {
				console.log(res)
				if(res.data.code=="1"){
					this.msgList=res.data.data
					this.markMsg()
				}else{
		
				}
				
			},
			complete() {
				uni.hideLoading();
			}
		});
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e

		},
		getbieren:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		getPostInfo:function(e){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+e
			});
		},
		markMsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=mark&class=pl", //请求标记已读消息
				data: {
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						console.log('已将信息标记为已读')
					}else{
						console.log('标记已读失败！')
					}
					
				},
				complete() {
					
				}
			});
		}
	}
}
